import React from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCreateElement,
  CSidebar,
  CSidebarBrand,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavTitle,
  CSidebarMinimizer,
  CSidebarNavDropdown,
  CSidebarNavItem,
  CImg,
} from "@coreui/react";

import CIcon from "@coreui/icons-react";

// sidebar nav config
import navigation from "./_nav";

const TheSidebar = () => {
  const dispatch = useDispatch();
  const show = useSelector((state) => state.sidebarShow);

  return (
    <CSidebar
      show={show}
      onShowChange={(val) => dispatch({ type: "setNavbar", sidebarShow: val })}
      style={{ backgroundColor: "#1e783c" }}
    >
      <CSidebarBrand className="d-md-down-none" to="/">
        <div className="c-avatar">
          <CImg
            src={"ugauss/uGauss.png"}
            className="c-avatar-img"
            alt="admin@bootstrapmaster.com"
          />
        </div>
        <div>
          <div
            className="px30"
            style={{ color: "white", padding: "10px 10px" }}
          >
            UGauss
          </div>
        </div>
        <CIcon
          className="c-sidebar-brand-minimized"
          name="sygnet"
          height={35}
        />
      </CSidebarBrand>
      <CSidebarNav>
        <CCreateElement
          items={navigation}
          /* style={{.hover : "#469c4d"}} */
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle,
          }}
        />
      </CSidebarNav>
    </CSidebar>
  );
};

export default React.memo(TheSidebar);
